#headshot{
    max-height: 400px;
    max-width: calc((3/7)*100);
    aspect-ratio: 2/3;
    object-fit: cover;
    border-radius: 5px;
    border: solid 3px rgba(245, 245, 245, 0.5);
    grid-column-end: 1;
    grid-row-start: 1;
    grid-row-end: 2;
}

#about {
    display: grid;
    grid-template-columns: 70% 30%;
    grid-column-gap: 5%;
}

#container{
    width: 100%;
}